.modal {
  .img-responsive {
    width: 100%;
  }
}

.modal-dialog {
  width: 920px;
}

.modal-header {
  border-bottom: none;

  .close {
    font-size: 25px;
    position: relative;
    right: -25px;
    top: -25px;
  }
}

.modal-content {
  padding: 35px;
}

.modal-title {
  font-family: 'Oswald', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #0054a6;
}

.modal-title-big {
  font-size: 27px;
}

.modal-title-small {
  margin-top: 5px;
  font-size: 16px;
}

.modal-text {
  font-size: 13px;
  color: grey;
  text-align: justify;
}

.modal-sub-title {
  font-family: 'Oswald', sans-serif;
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
  color: #898989;
  margin-bottom: 20px;
}

.modal-sub-title-small {
  font-size: 14px;
  &.color-black {
    font-size: 16px;
  }
}

.separate-section {
  margin-top: 30px;
}

.modal-register {
  color: #363636;

  .modal-header {
    margin-bottom: 15px;
  }

  .modal-body {
    padding-top: 0;
    padding-right: 130px;
    padding-left: 130px;
  }

  .modal-dialog {
    width: 920px;
  }
  .modal-title {
    color: #363636;
  }
  .section-separator {
    margin-left: -10px;
    margin-right: -10px;
    width: auto;
    max-width: none;
  }

  .modal-footer {
    border-top: none;
    padding-right: 130px;
    padding-left: 130px;
    padding-bottom: 0;

    button[type="submit"] {
      width: 100%;
      max-width: 175px;
    }

    a.btn {
      width: 100%;
      max-width: 180px;
      margin-top: 25px;
      font-weight: bold;
      font-family: 'Oswald', sans-serif;
      font-size: 20px;
    }
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  .section-separator {
    margin-top: 5px;
  }

  .bootstrap-select .dropdown-toggle {
    display: block;
    width: 100%;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857;
    color: #555;
    background-color: #fff;
    height: 35px;
    background-image: none;
    border: 1px solid #959595;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;

  }

  .dropdown-menu > li > a {
    padding: 10px 20px;
  }

}

.fa-asterisk {
  color: #e51b24;
}

.select-placeholder {
  font-size: 13px;
  text-transform: uppercase;
  color: #959595;
  background-color: transparent;
}

.text-important {
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 30px;
}

.modal-contact {
  .modal-sub-title:first-child {
    margin-top: 0;
  }

  .modal-sub-title {
    margin-bottom: 10px;
  }

  i.fa-map-marker {
    font-size: 23px;
  }
  i.fa-phone {
    font-size: 20px;
  }
  .modal-sub-title {
    font-size: 20px;
  }
}

.contact-ul {
  margin: 20px 0;
  li {
    margin: 10px 0;
    &:first-child {
      margin-bottom: 15px;
    }
  }
  i.fa {
    color: #0054a6;
    font-size: 17px;
    top: 0;
  }
}

.container-contact-ul {
  margin-top: 50px;
}

.ol-inside,
.ul-inside {
  padding-left: 0;
  list-style-position: inside;
}

ol.ol-number.start-number {
  counter-reset: foo;
}

.modal-thank .modal-dialog {
  max-width: 400px;
  width: 100%;

  .modal-body {
    p {
      font-size: 17px;
    }
  }
}


